#login-page{min-height:100vh;background-image:url(/social-app/static/media/marek-piwnicki-LGgKrtjNsoI-unsplash.86e442dd.jpg);background-position:center;background-size:cover;display:grid;grid-template-rows:1fr auto 1fr}@media screen and (min-width: 768px){#login-page{grid-template-columns:1fr 22em;grid-template-rows:1fr 1fr}}#login-page header{-webkit-align-self:flex-start;align-self:flex-start;margin:1em 1.3em;font-weight:bold;color:#3d315b}#login-page header h1{font-size:2.4em;margin-bottom:0.2em}@media screen and (min-width: 768px){#login-page header h1{margin-bottom:0.6em;font-size:3em}}#login-page header ul{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start;font-weight:normal;font-size:0.8em}@media screen and (min-width: 768px){#login-page header ul{font-size:0.9em}}#login-page header ul li{display:inline-block;color:#3d315b}@media screen and (min-width: 768px){#login-page header ul li{padding:0.4em 1em;background-color:#3d315b;color:white;border-radius:0.6em;margin-bottom:1.4em}}@media screen and (min-width: 768px){#login-page #login-form-container{grid-row:1 / 3;grid-column:2}}#login-page footer{-webkit-align-self:flex-end;align-self:flex-end;font-size:0.7em;margin:0.6em 1.6em;color:white}

@-webkit-keyframes spinning{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spinning{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.loading-icon svg{display:block;-webkit-animation:spinning 3s linear infinite;animation:spinning 3s linear infinite}

#login-form{margin:1em;padding:1.6em 1em;border:none;border-radius:0.8em;background-color:#f8f8f8;box-shadow:0 3px 8px #bbb}@media screen and (min-width: 768px){#login-form{height:100%;margin:0;border-radius:0;padding:1.6em 1.3em}}#login-form label{display:block;margin-bottom:0.6em;color:#3d315b}#login-form input{width:100%;height:3em;padding:0 0.6em;border:1px solid #bbb;border-radius:0.4em;margin-bottom:1em}#login-form input:focus{box-shadow:0 0 6px 3px #bbb}#login-form #login-button{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100%;height:3em;margin-top:2em;border:none;border-radius:0.4em;background-color:#3d315b;color:white}#login-form #login-button svg{font-size:2em}

#app-header{height:3.4em;background-color:#f8f8f8;display:grid;grid-template-columns:repeat(5, 1fr)}@media screen and (min-width: 768px){#app-header{height:4em;grid-template-columns:repeat(4, 8em) 1fr 8em}}#app-header a,#app-header button{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background:none;border:none;transition:background-color border-top 0.5s}#app-header a svg,#app-header button svg{font-size:1.6em}@media screen and (min-width: 768px){#app-header a svg,#app-header button svg{font-size:2em}}#app-header a span,#app-header button span{font-size:0.7em;display:none}@media screen and (min-width: 768px){#app-header a span,#app-header button span{display:block}}#app-header .active{background-color:white;border-top:4px solid white;border-bottom:4px solid #3d315b;color:#ff6b6b}#app-header #logout-button:active{color:#ff6b6b}@media screen and (min-width: 768px){#app-header #logout-button{grid-column:6}}

.loading-content{font-size:1.3rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.loading-content i{font-size:2.4em;margin-bottom:0.3em;color:#ff6b6b}.loading-content p{color:#3d315b}

.error-content{font-size:1.4rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.error-content h2{font-size:1.4em;color:#ff6b6b}.error-content p{color:#3d315b;margin-bottom:2em}.error-content a{padding:1em 2em;background-color:#3d315b;color:white;border-radius:0.4em}

.avatar-wrapper{height:1.8em;width:1.8em;position:relative}.avatar-wrapper .avatar{width:100%;height:100%;-webkit-filter:grayscale(50%);filter:grayscale(50%);border-radius:9999px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;overflow:hidden}.avatar-wrapper .profile-status{height:0.6em;width:0.6em;border:2px solid white;border-radius:9999px;position:absolute;bottom:-0.15em;right:-0.15em}.avatar-wrapper .profile-status.online{background-color:limegreen}.avatar-wrapper .profile-status.offline{background-color:lightgray}

.post .post-header{padding:0.8em;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center}.post .post-header .post-header-text{margin-left:1em;font-weight:normal}.post .post-body{padding:1em;border-radius:0 1em 1em;background-color:#f8f8f8}.post .post-body .post-date{font-size:0.8em;color:#555;margin-bottom:0.4em}.post .post-body .post-content{margin-bottom:1em}.post .post-body .post-statistics{font-size:0.8em;margin-bottom:1.4em}.post .post-comments{font-size:0.8em}

.content-statistics{color:#3d315b}.content-statistics.background-dark{color:white}.content-statistics .content-statistics-list{list-style:none;margin-bottom:1em}.content-statistics .content-statistics-list li{display:inline-block;border:none;border-radius:0.3em}.content-statistics .content-statistics-list li:not(:last-child){margin-right:1em}.content-statistics .content-statistics-list li strong{color:#ff6b6b}.content-statistics .reaction-notification{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;color:#ff6b6b}.content-statistics .reaction-notification svg{margin-right:0.4em}

.content-menu button{display:-webkit-inline-flex;display:inline-flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding:0.4em 1em;border-radius:0.4em;border:1px solid #3d315b;color:#3d315b;background-color:transparent;transition:all 0.3s}.content-menu button svg{margin-right:0.4em}.content-menu button:not(:last-child){margin-right:1em}.content-menu.background-dark button{border:1px solid white;color:white}.content-menu button:hover{color:#ff6b6b;border-color:#ff6b6b}.content-menu button:active{background-color:#ff6b6b;color:white}

.comment{display:grid;grid-template-columns:auto 1fr}.comment .comment-avatar{padding:0.6em}.comment .comment-body{justify-self:flex-start;background-color:#555;border-radius:0 1em 1em;color:white;padding:1em}.comment .comment-body .comment-date{font-size:0.8em;margin-bottom:0.6em}.comment .comment-body .comment-content{margin-bottom:1em}.comment .comment-body .comment-statistics{margin-bottom:1.2em}.comment .comment-body .comment-menu{font-size:1.1em}

.input-form{display:grid;grid-template-columns:1fr auto}.input-form textarea{padding:0.5em;border-radius:0.5em;border:1px solid #bbb;resize:vertical}.input-form button{-webkit-align-self:flex-end;align-self:flex-end;margin-left:0.6em;padding:0.5em 1em;background-color:#ffe66d;border-radius:0.5em;border:none}

.comments-menu{margin:1.2em 0 0.6em 3em}.comments-menu .new-comment-button{padding:0.6em 2em;border-radius:0.6em;border:none;background-color:#ffe66d;color:#3d315b}.comments-header{margin:0.4em 0 0.4em 3em;font-weight:normal}.comments-list{list-style:none}.comments-list>li{margin-bottom:0.6em}

#posts{padding:1em;margin:auto}@media screen and (min-width: 768px){#posts{padding:3em}}#posts #new-post-form{margin-bottom:2em;width:100%;max-width:60em}#posts #posts-list{width:100%;max-width:60em;list-style:none}#posts #posts-list>li{margin-bottom:2em}

.friend-card{height:6em;display:grid;grid-template-columns:auto 1fr;-webkit-align-items:center;align-items:center;background-color:#f8f8f8;border-radius:0.6em}.friend-card .friend-avatar{margin:1em}.friend-card .friend-info{text-align:left}

#friends-list{padding:1em}#friends-list h1{font-size:2em;margin-bottom:0.4em;color:#bbb}#friends-list #friends-cards{display:grid;grid-gap:1em;gap:1em;grid-template-columns:repeat(auto-fit, minmax(14em, 1fr))}

.profile-statistic-card{padding:0.6em;border-radius:0.6em}.profile-statistic-card .card-label{color:#3d315b}

@media screen and (min-width: 768px){#profile{display:grid;grid-template-columns:auto 1fr}}#profile #profile-avatar{margin:4em 1em 2em 1em;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}@media screen and (min-width: 768px){#profile #profile-avatar{margin:2em 1em;-webkit-align-self:flex-start;align-self:flex-start}}#profile #profile-header{margin:1em;padding:1em;background-color:#f8f8f8;border-radius:0.6em}#profile #profile-header .display-name{font-size:1.6em;color:#ff6b6b}#profile #profile-statistics{margin:1em;display:grid;grid-template-columns:repeat(auto-fill, minmax(10em, 1fr));grid-gap:1em;gap:1em}#profile #open-conversation-button{display:block;margin:1em;padding:0.6em;border-radius:0.6em;background-color:#ff6b6b;color:white;text-align:center}

.message{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;width:auto}@media screen and (min-width: 768px){.message{max-width:80%}}.message.sent{margin-left:auto;-webkit-justify-content:flex-end;justify-content:flex-end}.message .message-avatar{padding:0.6em}.message .message-content{padding:0.6em 1em;margin-bottom:0.6em;background-color:#ff6b6b;color:white;border-radius:0.6em}.message .message-date{font-size:0.8em;color:#555}.message.sent .message-content{background-color:#f8f8f8;color:#3d315b}.message.sent .message-date{text-align:right}

#conversation{padding:1em}#conversation h1{font-size:2em;margin-bottom:0.4em;color:#bbb}#conversation #messages-list{list-style:none}#conversation #messages-list li{margin-bottom:1.2em}@media screen and (min-width: 768px){#conversation #new-message-form{margin-top:2em}}

#friend-selector{padding:0.8em;background-color:#f8f8f8;border-right:1px solid #bbb}#friend-selector a{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;padding:0.6em}#friend-selector a:not(:last-child){border-bottom:1px solid #bbb}#friend-selector a .friend-name{margin-left:1em;margin-right:auto}#friend-selector a svg{font-size:2.6em;color:#ff6b6b}

#messages{height:100%}#messages #conversation-pane{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-size:2em;color:#bbb;text-align:center}#messages #selector-wrapper,#messages #conversation-wrapper{display:none;overflow-y:auto}#messages.contacts-view #selector-wrapper{display:block}#messages.conversation-view #conversation-wrapper{display:block}@media screen and (min-width: 768px){#messages{display:grid;grid-template-columns:24em 1fr}#messages #selector-wrapper,#messages #conversation-wrapper{display:block}}

#app-page{display:grid;height:100vh;width:100vw;grid-template-rows:auto 1fr}#app-page #app-content-wrapper{overflow-y:auto}

html{font-size:62.5%}body{font-size:1.6rem}*{box-sizing:border-box;margin:0;padding:0;font-family:"Poppins",sans-serif;font-size:inherit;color:inherit}a{text-decoration:none}button{cursor:pointer}

